@charset "UTF-8";
body {
  font-size: 1.4rem;
  background: #fff;
  font-family: -apple-system, "BlinkMacSystemFont", "Helvetica Neue", Helvetica, "Arial", "ヒラギノ角ゴ ProN W3", "Hiragino Kaku Gothic ProN", "メイリオ", Meiryo, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
}

p {
  margin-block-start: 0;
}

html,
body {
  height: 100%;
}

.heading {
  text-align: center;
  font-size: 4rem;
}

.summary {
  font-size: 1.1rem;
}

#first {
  position: relative;
  min-height: 100vh;
  background: #F3C0AB;
  font-size: 2rem;
}

.first-nav {
  background: #fff;
}

.brand-img {
  max-height: 56px;
}

@media only screen and (min-width: 601px) {
  .brand-img {
    min-height: 64px;
  }
}

.nav-toggle {
  color: #df81a2;
}

.sidenav {
  padding-top: 3rem;
}

.first-card-height {
  height: calc(100vh - 200px);
}

.first-card {
  background: #ffffffc4;
  align-items: center;
}

.first-down {
  position: relative;
  bottom: 15px;
  color: #fff;
}

.first-down-icon {
  font-size: 3rem;
  padding: 0.6rem 0.8rem;
  cursor: pointer;
  color: #df81a2;
  border: 0.3rem solid #df81a2;
  border-radius: 50%;
}

#about {
  min-height: 100vh;
  padding: 6.4rem 0;
  background: url("../img/bg.jpg") fixed;
}

.about-img {
  height: 14rem;
  padding: 1.5rem;
}

.about-img-around {
  margin-top: 3rem;
  margin-right: auto;
  margin-left: auto;
  height: 14rem;
  width: 14rem;
  background: #fff;
  border-radius: 50%;
  box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
}

.about-write {
  margin-top: 3rem;
  font-size: 1.4rem;
  padding: 0 1rem;
}

@media (max-width: 1000px) {
  .about-write {
    text-align: center;
    font-size: 1.2rem;
  }
  .about-br-hidden {
    display: none;
  }
}

.about-details {
  margin-top: 8rem;
}

.about-link {
  font-size: 5rem;
  color: #000;
}

#skills {
  padding: 6.4rem 0;
}

.skill-heading {
  font-size: 3rem;
}

.skills-card {
  padding: 5px 3px 0 3px;
}

.skills-img {
  height: 100px;
  width: 300px;
}

#exp {
  background: url("../img/bg.jpg") fixed;
  padding: 6.4rem 0;
  font-size: 16px;
}

.exp-timeline {
  position: relative;
  padding-bottom: 2px;
}

.exp-timeline:before {
  position: absolute;
  top: 0;
  width: 5px;
  height: 100%;
  content: '';
  background: #df81a2;
}

.timeline-item {
  margin-left: 40px;
}

.timeline-year {
  font-family: serif;
  font-size: 7rem;
  font-weight: 500;
  position: relative;
  margin: 0;
  letter-spacing: 3px;
  color: rgba(233, 30, 98, 0.301);
}

.timeline-year::before {
  position: absolute;
  top: 50%;
  left: -45px;
  width: 15px;
  height: 15px;
  content: '';
  border: 3px solid #e91e63;
  background: #fff;
}

.timeline-title {
  font-size: 2rem;
  font-weight: 600;
  margin-top: -70px;
  margin-bottom: 10px;
  color: #880e4f;
}

.timeline-desc {
  font-size: 1rem;
  line-height: 20px;
  padding-left: 8px;
  border-left: 2px solid #e91e63;
}

#contact {
  padding: 6.4rem 0;
  font-size: 16px;
}

.contact-form {
  margin-top: 5rem;
}

.contact-submit {
  background-color: #df81a2;
}

.contact-submit:hover {
  background-color: #d36f92;
}

.contact-submit:focus {
  background-color: #d14c7b;
}

#last {
  background: #df81a2;
}

.last-footer {
  padding-top: 2rem;
  padding-bottom: 1rem;
}

.copyright {
  font-size: 1.2rem;
  color: #fff;
}

.capriola {
  font-family: 'Capriola', sans-serif;
}

/*# sourceMappingURL=style.css.map */